import React, { Component} from 'react'
import { MdMessage } from "react-icons/md";
import { MdNotificationsNone } from "react-icons/md";
import { AiFillQuestionCircle } from "react-icons/ai";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { IoReorderThreeOutline } from "react-icons/io5";
import { IoPeopleOutline } from "react-icons/io5";
import RichTextEditor from 'react-rte';

const validUrlRegrex=  RegExp(/((([A-Za-z]{3,9}:(?:\/\/)?)(?:[-;:&=\+\$,\w]+@)?[A-Za-z0-9.-]+|(?:www.|[-;:&=\+\$,\w]+@)[A-Za-z0-9.-]+)((?:\/[\+~%\/.\w-_]*)?\??(?:[-\+=&;%@.\w_]*)#?(?:[\w]*))?)/)
const validateForm =(errors)=>{
    let valid=true;
    Object.values(errors).forEach(
        (val) =>val.length > 0 && (valid =false)
    );
    return valid;
}

class Page extends Component {

    constructor(props) {
        super(props);
    
        this.state = {
            valuee:'',
            Title:'',
            rtevalue: RichTextEditor.createEmptyValue(),
            choice:'',
            summary:'',
            registration:'',
            onlineLink:'',

            fileInput:'',
            startDate:'',
            endDate:'',
            starttime:'',
            endtime:'',
            discriptionbox:'',
            errors: {
                Title: '',
                summary:'',
                registration:'',
                onlineLink:''
              }
        }
        
    }
    handleChange=(event)=>{
        event.preventDefault();
        const {name,value}=event.target;
        let errors=this.state.errors;
        switch(name){
            case 'Title':
                errors.Title = 
                value.length < 11 
                ? 'Title should be minimum of 10 charaters'
                : '';
            break;
            case 'summary':
                errors.summary = 
                value.length < 21
                ? 'Summary should be minimum of 20 charaters'
                : '';
            break;
            case 'registration':
                errors.registration = 
                value.length < 11 
                ? 'registration should be minimum of 10 charaters'
                : '';
            break;
            case 'onlineLink':
                errors.onlineLink =
                validUrlRegrex.test(value)
                ? ''
                : 'This is not url';
            break;
            default:
            break;
        }
        this.setState({errors, [name]: value});
        /* this.setState({
            [event.target.name]:event.target.value
        }) */
    }
    
    onChange = (rtevalue) => {
        this.setState({rtevalue});
        if (this.props.onChange) {
          this.props.onChange(
            rtevalue.toString('html')
          );
        }
      };
    
    submitHandler=(event)=>{
         /* event.preventDefault(); */ 
        alert('Event is at '  +this.state.valuee+" "+this.state.fileInput+" "+this.state.startDate+" "+this.state.endDate+" "+this.state.starttime
        +" "+this.state.endtime); 
    }

    upload(event)
    {
        this.setState({
            fileInput:event.target.files[0].name
        })
    }
    StartdateHandler=(event)=>{
        this.setState({
            startDate:event
        })
    }
    endDateHandler=(event)=>{
        this.setState({
            endDate:event
        })
    }

    render() {
        const {errors}=this.state;
        return (
            <div className='container'>
            <div style={{backgroundColor:'rgb(255, 191, 0)'}}>
                
                <span style={{marginRight:'300px'}}><IoReorderThreeOutline/></span>
                <input className='col-md-5 form-group container' placeholder='Search' type='search' />
                
                <span><IoPeopleOutline/></span>
                <span><MdNotificationsNone/></span>
                <span style={{marginRight:'170px'}}><MdMessage color='black'/></span>
            </div>
            <div style={{float:'left'}}>Create Event</div>
            <div style={{float:'right'}}><button className="btn btn-primary">Create</button>&nbsp;&nbsp;&nbsp;
                <button className="btn btn-danger">Cancel</button></div>
                <br/><br/>
                <hr/>
                <form autoComplete='off' onSubmit={this.submitHandler}>

               
            
                
                    <div className='col-md-8 form-group container'>
                    <label className='float-left block-text text-darken-2 s'>
                    Add Event in*
                    </label>
                <select value={this.state.value} name="valuee" onChange={this.handleChange} className='form-control' required>
                    <option value=''>Select...</option>
                    <option value='10am'>10am</option>
                    <option value='12pm'>12am</option>
                    <option value='2pm'>2pm</option>
                    <option value='4pm'>4pm</option>
                </select>
                    </div>

                    <div className='col-md-8 form-group container'>
                        <label className='float-left block-text text-darken-2 s'>Title*</label>
                        <input type='text' name='Title' onChange={this.handleChange} className='form-control' required />
                        <label style={{marginLeft:'673px'}}>0/250</label>
                        {errors.Title.length > 0 &&
                <span style={{color:'red'}}>{errors.Title}</span>}
                    </div>

                    <div className='col-md-8 form-group container'>
                    <label className='float-left block-text text-darken-2 s'>
                    Categories*
                    </label>
                <select value={this.state.option} name='choice' onChange={this.handleChange} className='form-control' required>
                    <option option=''>Select...</option>
                    <option option='A'>A</option>
                    <option option='B'>B</option>
                    <option option='C'>C</option>
                    <option option='C'>C</option>
                </select>
                    </div>

                    <div className='col-md-8 form-group container'>
                        <label className='float-left block-text text-darken-2 s'>Short Summary*</label>
                        <input type='text' name='summary'  onChange={this.handleChange} className='form-control' required />
                        <label style={{marginLeft:'673px'}}>0/500</label>
                        {errors.summary.length > 0 &&
                <span style={{color:'red'}}>{errors.summary}</span>}
                    </div>
                    <div style={{textAlign:'initial',marginLeft:'200px'}}><strong><pre>Type:  Public</pre></strong></div>

                    <div className='col-md-8 form-group container'>
                     <input type='text' name='registration' onChange={this.handleChange} placeholder='Registration Site' className='form-control' required />
                     <label style={{marginLeft:'673px'}}>0/1024</label>
                     {errors.registration.length > 0 &&
                    <span style={{color:'red'}}>{errors.registration}</span>}
                     </div>

                    <div className="switch" style={{textAlign:'initial',marginLeft:'200px'}}>
                     <pre><label>
                      <strong style={{color:'black'}}>Is this a virtual event?   Yes</strong>   
                     <input type="checkbox" checked/>
                     <span className="lever"></span>
                     </label></pre>
                     </div>

                     <div className='col-md-8 form-group container'>
                     <input type='text' name='onlineLink' onChange={this.handleChange} placeholder='Online Link' className='form-control' required />
                     {errors.onlineLink.length > 0 &&
                <span style={{color:'red'}}>{errors.onlineLink}</span>}
                     </div>

                    <div className='col-md-8 form-group container'>
                    <div className="row">
                        <div className='col-sm-8'>
                        <label className='float-left block-text text-darken-2 s'>
                    Select Timezone*
                    </label>
                    <input type='text' value='Pune' onChange={this.handleChange} placeholder='Online Link' className='form-control' required />
                    </div>
                        <div className="col-sm-3"><br/><br/>
                        <pre><strong>GMT+05:30<AiFillQuestionCircle/></strong></pre>
                        </div>
                    </div>
                     </div >

                    <div className='col-md-8 form-group container'>
                        <div className="row">
                            <div className="col-sm-4">
                            <label className='float-left block-text text-darken-2 s'>
                            Start Date*
                            </label>
                            <DatePicker selected={this.state.startDate} onChange={this.StartdateHandler} placeholderText="Start date" />
                    </div>
                            <div className="col-sm-4">
                            <label className='float-left block-text text-darken-2 s'>
                            Start Time*
                            </label>
                             <input type='time' name='starttime' value={this.state.starttime} onChange={this.handleChange}/>
                    </div>
                        </div>
                    </div>

                    <div className='col-md-8 form-group container'>
                        <div className="row">
                            <div className="col-sm-4">
                            <label className='float-left block-text text-darken-2 s'>
                            End Date*
                            </label>
                            <DatePicker selected={this.state.endDate}  onChange={this.endDateHandler}  placeholderText="End date" />
                    </div>
                            <div className="col-sm-4">
                            <label className='float-left block-text text-darken-2 s'>
                            End Time*
                            </label>
                             <input type='time' name='endtime' value={this.state.endtime} onChange={this.handleChange} />
                    </div>
                        </div>
                    </div>

                    {/* <RichTextEditor style={{height:'200%'}}
                      value={RichTextEditor.createEmptyValue()}>
                    </RichTextEditor> */}

                    <div className="container">
                    <RichTextEditor
                    placeholder="Description (Max limit 8000 characyers)*"
                        value={this.state.rtevalue}
                        onChange={this.onChange}
                    />
                    <label style={{marginLeft:'900px'}}>0/8000</label>
                    </div>
                    

                    <label>
                    Upload Image*:
                <input type='file' name='fileInput' onChange={(e)=>this.upload(e)}/>

                       
                </label><br/><br/>
                <input type='submit' value='Submit'/>

                </form>
            </div>
        )
    }
}
export default Page